<template>
    <div
        class="routeStep"
        :class="tripStepPartClasses(step)"
        :style="{ borderLeftColor: tripStepBusColor(step) }"
    >
        <div v-if="step.travel_mode === 'WALKING'">
            <p>
                <span class="mdi mdi-walk"></span>
                {{ toDurationFormat(step.duration.value) }}
            </p>
        </div>
        <div v-if="step.travel_mode === 'TRANSIT'">
            <div v-if="isBus">
                <p class="startEnd">
                    <span class="left">{{ toTime(step.transit_details.departure_time.value ) }}</span>
                    <span
                        class="busLine"
                        :style="{ backgroundColor: step.transit_details.line.color, color: step.transit_details.line.text_color }"
                    >
                        <span class="mdi mdi-bus"></span>
                        {{ step.transit_details.line.short_name }}
                    </span>
                    <span class="right">{{ toTime(step.transit_details.arrival_time.value ) }}</span>
                </p>
                <p class="startEnd">{{ step.transit_details.headsign }}</p>
            </div>
            <div v-if="isTrain">
                <p class="startEnd">
                    <span class="left">{{ toTime(step.transit_details.departure_time.value ) }}</span>
                    <span
                        class="busLine"
                        :style="{
                        backgroundColor: tripStepPartClasses(step).includes('ic') ? '#cdf321' : '#2196f3',
                        color: tripStepPartClasses(step).includes('ic') ? '#000000' : '#ffffff'
                    }"
                    >
                        <span class="mdi mdi-train"></span>
                        {{ step.transit_details.line.short_name }}
                    </span>
                    <span class="right">{{ toTime(step.transit_details.arrival_time.value ) }}</span>
                </p>
                <p class="startEnd">
                    {{ step.transit_details.trip_short_name }},
                    {{ step.transit_details.headsign }}
                </p>
                <p class="startEnd emptyMiddle">
                    Spoor 1
                    ‏‏‎
                    <span class="mdi mdi-arrow-expand-right"></span>
                    <span class="right">{{ step.transit_details.departure_stop.name }}</span>
                </p>
                <p class="startEnd emptyMiddle">
                    Spoor 4
                    ‏‏‎‏‏‎
                    <span class="mdi mdi-arrow-collapse-right"></span>
                    <span class="right">{{ step.transit_details.arrival_stop.name }}</span>
                </p>
                <div v-if="additionalDataAvailable">
                    <p class="startEnd" @click="additionalDataOpen = !additionalDataOpen">
                        <span v-if="!additionalDataOpen">
                            <span class="mdi mdi-chevron-down"></span>
                            meer info
                        </span>
                        <span v-else>
                            <span class="mdi mdi-chevron-up"></span>
                            minder info
                        </span>
                    </p>
                    <div v-if="additionalDataOpen">
                        <p class="startEnd emptyMiddle">
                            <span class="mdi mdi-tape-measure"></span>
                            lengte
                            <span class="right">
                                <b>{{ additionalData.lengte }}</b> delen
                            </span>
                        </p>
                        <p class="startEnd emptyMiddle" v-for="materialPart in additionalData.materieeldelen" :key="materialPart.materieelnummer">
                            <span class="mdi mdi-barcode"></span>
                            treinstel <b>{{ materialPart.materieelnummer }}</b>
                            <span class="right">
                                <span class="mdi" :class="facilityIcon(facility)" v-for="facility in materialPart.faciliteiten" :key="facility + materialPart.materieelnummer"></span>
                            </span>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {
    tripStepPartClasses,
    toDurationFormat,
    tripStepBusColor,
    toTime
} from "./shared.js";

import nsCall from "../../js/nsCall.js";

export default {
    props: ["step"],
    data() {
        return {
            additionalData: {},
            additionalDataAvailable: true,
            additionalDataOpen: false,
            isTrain: false,
            isBus: false
        }
    },
    methods: {
        tripStepPartClasses,
        toDurationFormat,
        tripStepBusColor,
        toTime,
        facilityIcon(facility) {
            if (facility === "WIFI") {
                return ["mdi-wifi-strength-4"];
            } else if (facility === "TOILET") {
                return ["mdi-human-male-female"];
            } else if (facility === "STILTE") {
                return ["mdi-volume-mute"];
            } else if (facility === "FIETS") {
                return ["mdi-bicycle"];
            } else if (facility === "STROOM") {
                return ["mdi-power-socket-eu"];
            }
        }
    },
    async created() {
        this.isTrain = tripStepPartClasses(this.step).includes('train');
        this.isBus = tripStepPartClasses(this.step).includes('bus');

        if (this.isTrain) {
            var data = await nsCall("/virtualTrain/" + this.step.transit_details.trip_short_name);

            this.additionalData = Object.assign({}, this.additionalData, data);

            this.additionalDataAvailable = true;
        }   
    }
};
</script>

<style lang="scss" scoped>
@import "../../scss/config.scss";

.routeStep {
    min-height: 16px;
    width: calc(100% - 32px);

    padding: 4px 16px;

    border-left: 2px dotted #9b9b9b;

    &.transport {
        border-left-style: dashed;

        &.train {
            border-left-style: solid;

            &.ic {
                border-left-color: #cdf321;
            }

            &.spr {
                border-left-color: #2196f3;
            }
        }
    }

    &.end {
        border-left-color: transparent;

        p {
            font-size: 20px;
        }
    }

    h4 {
        margin: 4px 0;

        font-size: 20px;
        font-family: $hFont;
    }

    p {
        font-size: 16px;
        font-family: $pFont;

        .busLine {
            padding: 2px 8px;
            border-radius: 5px;
        }

        &.startEnd {
            text-align: center;

            &.emptyMiddle {
                text-align: left;
            }

            .left {
                float: left;
            }

            .right {
                float: right;

                .mdi + .mdi {
                    margin-left: 4px;
                }
            }
        }
    }
}
</style>